import React, {Component} from 'react';
// import 'bootstrap/dist/css/bootstrap.min.css'
import NavMain from './components/Nav/Nav';
import Header from './components/Header/Header';
import Content from './components/Content/Content';
import Footer from './components/Footer/Footer';
import Profile from './components/Profile/Profile';
import RegistrForm from './components/Registration/RegistrForm';
import Posts from './components/Posts/Posts';
import ProfileNav from './components/ProfileNav/ProfileNav';
import Comunity from './components/Comunity/Comunity';


// import {Container, Row, Col} from 'react-bootstrap';

import './App.css';
export default class App extends Component {

  state = {
    RegistrForm: {
      display: 'block',
      overflow: 'hidden',
      width: '100%',
      height: '100vh',
      backgroundColor: 'rgba(255, 255, 255, 0.9)',
      position: 'fixed',
      zIndex: '700',
      top: '-100%',
      transition: 0.5 + 's'
    },
    allo: 'true'
  }

  registrHandler = (state)=> {
    const oldState = Object.assign({}, state)
    const RegistrForm = oldState
    
    if (state.top === '-100%') {
      document.body.style.overflow = 'hidden'
      RegistrForm.top = '0%'
      this.setState({RegistrForm})
    } else {
      document.body.style.overflow = 'scroll'
      RegistrForm.top = '-100%'
      this.setState({RegistrForm})
    }
  };

  render() {
    return (
      <div className="app-wrap">
        <RegistrForm 
        style={this.state.RegistrForm}
        />
        <NavMain registrHandler={this.registrHandler.bind(this, this.state.RegistrForm)}/>
        <Header />
        <Content />
        <Footer />
      </div>
    )
  }
}
