import React from 'react';
import './Header.css';

const Header = ()=> {
    var style = {
        fontSize: 2.7 + '+em',
        margin: 0 
    }
    return (
        <header>
        <div className="head-bg">
            <div className="overlay"></div>
            <div className="header_content">
                <h1>Best Sellings Here</h1>
                <div className="desc">
                    <p >Sell your items fo FREE!!!</p>
                    <a href="#" className="links links-1">sign up via vk</a>
                    <a href="#" className="links links-2">telegram</a>
                </div>
            </div>
        </div>
        <section className="filters">
            <div className="container">
                <div className="filter-content">
                    <div className="filter_left">
                        <form action="">
                            <input type="search" placeholder="Find Seller or Items ..." />
                        </form>
                    </div>
                    <div className="filter_right">
                        <div className="filter_right-content">
                            <div className="drop-down">
                                <i></i>
                                <span>Categories</span>
                            </div>
                            <div className="drop-down">
                                <i></i>
                                <span>All Creative Categories</span>
                            </div>
                            <div className="drop-down">
                                <i></i>
                                <span>Most Recant</span>
                            </div>
                            <div className="drop-down">
                                <i></i>
                                <span>Worldwide</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </header>
    )
};

export default Header;