import React from 'react';
import './RegistrForm.css';

const RegistrForm = (props) => {
    let styles = props.style;
    
    return (
        <div style={styles} className='RegistrForm'>
            <div className="form_wrap_reg">
                <form action="" className='reg_form'>
                    <input type="email" name='email'placeholder='Email'/>                   
                    <input type="password" name='passw' placeholder='Password'/>
                    <input type="text" name='username' placeholder='Username'/>
                    <input type="password" name='repPassw' placeholder='Repeat Password'/>
                    <div className="sumbit_wrap">
                        <input type="submit" name='send' value='Send'/>
                    </div>
                </form>
            </div>
        </div>
    )
};

export default RegistrForm;